<template>
  <div class="index" >
  	<!--<div style="max-width: 750px;margin: auto;height: 40px;background: #ccc;"></div>-->
    <mt-swipe :show-indicators="false" :auto="3000" class="banner">
      <mt-swipe-item v-for="(item,key) in carousel" :key="key" >
        <div class="index_banner">
          <img :src="item.path" alt="" @click="enterUrl(item.url)">
        </div>
      </mt-swipe-item>
    </mt-swipe>
    <!--<img src="../../assets/shops/imgs/img-goods2.png"/>-->
    <!--<section class="title">
      <img class="star" src="../../assets/shopImage/start.png">
      <span class="title-desc">特色专区</span>
    </section>-->
    <div class="title-goods">活动专区</div>
    <div class="goods" style="background: #fff;">
      <section class="goods-l" @click="count">
        <p class="title-desc goods_m">今日剁手价</p>
        <p class="title-info goods_m">品牌好货 深度折扣</p>
        <p class="goods_m" style="min-height: 0.4rem">
          <strong class="countdown" v-if="content">{{content}}</strong>
        </p>
        <section class="goods_l_img">
          <img :src="countInfo.path" alt="" width="85">
        </section>
      </section>
      <section class="goods_r">
        <div class="goods_top" style="background: #f8f8f8;">
          <img :src="title.path" alt=""  @click="seckill">
          <div style="margin-top:20%;margin-left: 10%;">
          	{{time}}
          </div>
        </div>
        <div class="goods_down" style="background: #f8f8f8;">
          <img :src="offer.path" alt=""  @click="lottery">
        </div>
      </section>
    </div>
    <div class="title-goods">商品推荐</div>
    <ul class="goodslists">
			<li v-for="item in items" @click="goodInfo(item)">
				<!--<div style="min-height: 140px;width: 100%;overflow: hidden;">-->
				<img :src="item.pic_url" />
				<!--</div>-->
				<div class="over2">{{item.good_name}}</div>
				<div class="goodsprice">
					<span>￥</span>{{item.good_price}}<span>.00</span>
				</div>
			</li>
			<div class="clear"></div>
		</ul>
    <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
      <li class="goods_list" v-for="(item, key) in goodsList" @click.stop="seeInfo(item)">
        <section class="goods_list_l">
          <img :src="item.pic_url" alt="">
        </section>
        <section class="goods_list_r">
          <div class="goods_desc">
            <p>{{item.good_name}}</p>
            <p></p>
          </div>
          <div class="goods_price">
            <p>¥ {{item.good_price}}</p>
            <img src="../../assets/shopImage/shop_car.png" alt="" width="34" @click.stop="addCar(item)">
          </div>
        </section>
      </li>
      <!--底部判断是加载图标还是提示“全部加载”-->
      <li class="more_loading" v-show="!queryLoading">
        <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
        <span v-show="allLoaded">已全部加载</span>
      </li>
      <!--<img src="../../config/bianji.png"/>-->
    </ul>
  </div>
</template>
<script>
	
  import { Toast,InfiniteScroll} from 'mint-ui';
  import { Indicator} from 'mint-ui';
export default{
    data(){
      return {
      		items:[
							
					],
          lists: [
            {pic: '01.png', name: '尖端好货'},
            {pic: '02.png', name: '会员专享'},
            {pic: '03.png', name: '品质保障'},
            {pic: '04.png', name: '尊贵体验'}
          ],
        moreLoading: false,
        queryLoading: false,
        allLoaded: false,
        pageSize: 20,
        pageNum: 1,
        totalNum: 10,
        //商品列表
        goodsList: [],
        carousel: [],
        content:'',
        type:'',
        countInfo:{path:'',url:''},
        title:{path:'',url:''},
        offer:{path:'',url:''},
        time:''
      }
    },
  methods:{
    enterUrl(url) {
      if (url !== undefined && url !== '') {
        location.href = url;
      }
    },
    
    getGoodsLists(page){
    	this.post('home/Index/getShopList',{page:page,number:20}).then(function(res){  	
    		if(res.data.code===10000){
    			this.items=res.data.info.data;    		
    			
    			this.items.forEach(function(item){
    				item.pic_url=this.imageURL(item.pic_url)
    				
    			}.bind(this))
    		}
    	}.bind(this))
    },
    

    // 获取抢购时间
    getCountTime(){
      this.post('home/Shop/getQgTime', {}).then(res => {
        if (res.data.code === 10000) {
          let start = res.data.info.start;
          let end = res.data.info.end;
          this.countDown(end);
        }
      })
    },
      //查看商品详情
    goodInfo(item){
      let url = '/goodInfo/' + item.good_id;
      this.$router.push(url);
    },
    //秒杀商品
    count(){
        if (this.type !== 0) {
          this.$router.push('/count')
        } else {
          Toast({
            message: '请先去开店',
            duration: 1000
          });
        }
    },
    lottery(){
    	this.$router.push('/lottery')
    },
    seckill(){
    	this.$router.push('/seckill')
    },
//  goodInfo(){
//  	this.$router.push('/goodInfo')
//  },
    //下拉刷新
    loadMore(){
//    if(this.allLoaded){
//      this.moreLoading = true;
//      return;
//    }
//    if(this.queryLoading){
//      return;
//    }
//    this.moreLoading = !this.queryLoading;
//    this.pageNum++;
//    this.getGoodsList(this.pageNum);
    },
    addCar(item){
        this.post('home/Shop/addCart', {
          good_id: item.id,
          number: 1
        }).then(res => {
          if (res.data.code === 10000){
            Toast({
              message: '成功加入购物车',
              duration: 1000
            });
          } else {
            Toast({
              message: res.data.info,
              duration: 1000
            });
          }
        });

    },
    
//  getGoodsList(page = 1){
//      this.post('wxapi/Index/getShopList', {
//        page: page,
//        number: 10
//      }).then(res => {
//        if (res.data.code === 10000){
//            this.totalNum = res.data.info.number;
//            this.goodsList = this.goodsList.concat(res.data.info.data);
//            this.goodsList.forEach(item => {
//              if (item.pic_url.indexOf('http') !== -1) {
//
//              } else {
//                item.pic_url = this.imageURL(item.pic_url);
//              }
//            });
//            this.allLoaded = this.goodsList.length=== this.totalNum;
//            this.moreLoading = this.allLoaded;
//        }
//      })
//  },
    //获取轮播图
    getCarousel(){
      this.post('home/Index/getShowImg',{}).then(res => {
//    	console.log(res)
        if (res.data.code === 10000) {
          this.carousel = res.data.info;
          this.carousel.forEach(item => {
            item.path = this.imageURL(item.path);
          })
        }else if(res.data.code === 11111){
        	let redirectUrl = encodeURIComponent('http://http://nhsz.xiguawenhua.com/shop/#/wait')
          window.location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx6f8b18842e1d86e0&redirect_uri='+redirectUrl+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
        }
//      console.log(res)
      })
    },
    
   
  },
  filters: {
    imgFilter(val){
      return 'static/shop/' + val;
    }
  },
  created(){
    
    this.getCarousel();
  },
  mounted(){
//   console.log(this.countTime)
     var self=this
     this.countTime(1522752040647/1000,function(date){
     	self.time=date
     })
     
     this.getGoodsLists(1)
  }
}
</script>
<style scoped>
  .banner{
    height: 2.4rem;
    background-color: #F2F2F2;
  }
  .index_banner {
    width: 100%;
    padding-bottom: 2.4rem;
    position: relative;
  }
  .index_banner img {
    width: 100%;
    min-height: 100%;
    position: absolute;
    top: 0;
  }
  .title:before,.title:after {
    content: '';
    width: 0.2rem;
    height: 1px;
    background-color: #666666;
    display: inline-block;
    vertical-align: middle;
    margin: 0 10px;
  }
  .title {
    text-align: center;
    padding: 10px 0;
  }
  .star {
    display: inline-block;
    margin-right: 4px;
    width: 0.34rem;
    vertical-align: middle;
  }
  .title-desc {
    color: #111111;
    font-weight: 900;
    font-size: 0.3rem;
    vertical-align: middle;
  }
  .title-info {
    font-size: 0.25rem;
    color: #555555;
    margin: 5px 0;
  }
  /*每日特价*/
  .goods {
    padding:0.2rem;
    padding-top: 0;
    display: flex;
    justify-content: center;
  }
  .goods-l {
    padding-top: 0.2rem;
    background-color: #ffefe2;
    flex: 1;
  }
  .goods_m {
    padding: 0 0.2rem;
  }
  /*倒计时*/
  .countdown {
    display: inline-block;
    height: 0.4rem;
    background-color: #4D4343;
    color: #FFFFFF;
    font-size: 0.22rem;
    text-align: center;
    line-height: 0.4rem;
    padding:0 0.1rem;
  }
  .day-btn {
    display: inline-block;
    font-size: 0.2rem;
    color: #ffffff;
    background-color: orange;
    padding: 2px 0.2rem;
    border-radius: 5px;
  }
  .day_t {
      background-color: #ada1d9;
  }
  .day_y {
    background-color: #d7B85e;
  }
  .goods_l_img {
    width: 100%;
    height: 2.5rem;
    position: relative;
    overflow: hidden;
    bottom: -0.25rem;
  }

  .goods_l_img>img {
    position: absolute;
    top: 0;
    width: 100%;
    max-height: 100%;
  }
  /*清除浮动*/
  .goods-l:before {
    display: inline-block;
    overflow: hidden;
    clear: both;
  }
  /*右侧*/
  .goods_r {
    flex: 1;
    margin-left: 0.1rem;
  }
  .goods_top {
    height: 2.1rem;
    flex: 1;
    /*background-color: #f0eef9;*/
    margin-bottom: 0.1rem;
    position: relative;
    overflow: hidden;
  }
  .goods_down {
    height: 2.1rem;
    flex: 1;
    position: relative;
    overflow: hidden;
    /*background-color: #fef8e2;*/
  }
  .goods_top img, .goods_down img{
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 0;
  }
  /*分类*/
  .sorts {
      padding: 0.2rem;
      border-bottom: 1px solid #f2f2f2;
  }
  .list-name {
    font-size: 0.2rem;
    color: #555555;
  }
  /*商品列表*/
  .goods_list {
    padding: 0 10px;
    border-bottom: 1px solid #f5f5f5;
    display: flex;
    margin-bottom: 10px;
  }
  .goods_list_l {
    width: 2rem;
    padding-top: 2rem;
    position: relative;
    background-color: #F2F2F2;
    margin-bottom: 0.2rem;
  }
  .goods_list_l>img {
    position: absolute;
    top: 0;
    width: 100%;
    max-height: 100%;
  }
  .goods_list_r {
    flex: 1;
    margin-left: 0.2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;;
  }
  .goods_desc>p:first-child {
    font-size: 0.27rem;
    color: #111111;
  }
  .goods_desc>p:nth-child(2) {
    color: #666666;
    font-size: 0.25rem;
  }
  .goods_price {
    margin-bottom: 0.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .goods_price>p {
    font-size: 0.32rem;
    color: #ff6f00;
  }
  /*加入购物车动画*/
  .more_loading {
    display: flex;
    justify-content: center;
    margin-bottom: 0.1rem;
  }
  
  /*商品列表*/
.title-goods{background: #fff;padding: .2rem;margin-top: .2rem;}
.goodslists{padding-right: 10px;background: #fff;}
.goodslists li{float: left;width: 50%;text-align: center;margin-right: 10px;margin-bottom: 10px;padding: 5px 0;text-align: left;}
.goodslists li:nth-of-type(2n){margin-right: -10px;}
.goodslists li img{display: block;width: 100%;}
.clear{clear: both;}
.over2{overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 2;}
.goodsprice{color: #FF5773;font-size: .35rem;}
</style>



